.newsHr .category {
    right: 0;
}
.newsHr h1, .newsHr h2, .newsHr h3, .newsHr h4, .newsHr h5, .newsHr h6, .newsHr p {
    margin: 0 !important;
}
.newsHr {
    max-width: 100%;
    padding: 0 0 0 0;
}
.newsHr .cart span {
    position: absolute;
    font-size: 0.7em;
    background: rgb(0, 0, 0, 0.5);
    padding: 4px 6px;
    font-weight: bold;
    top: 0;
}
.newsHr .cart:hover span {
    background: rgb(0, 0, 0, 0.7);
    transition: 0.4s;
    color: rgb(224, 164, 79);
}
.newsHr .cart {
    width: 100%;
    height: 250px;
    position: relative;
    transition: 0.6s;
    margin: 20px 0 0 0;
}
.newsHr .cart .img {
    width: 100%;
    height: 100%;
    box-shadow: inset 0px -80px 100px 1px #000;
    background: linear-gradient(to right, transparent, #000, transparent);
    transition: 0.6s;
    overflow: hidden;
}
.newsHr .cart .img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.8;
    transition: 0.6s;
}
.newsHr .cart h3 {
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 1em;
    line-height: 1.1;
    min-width: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 10px;
    box-shadow: inset 0px -50px 100px 1px #000;
    transition: 0.6s;
    font-weight: bold;
}
.pagination {
    margin: 60px auto 0 auto;
    display: flex;
    overflow: scroll;
    max-width: 100%;
    padding: 0 0 20px 0;
    justify-content: center;
}
.pagination a {
    margin: 0 10px;
    border: 2px solid rgb(204, 135, 45);
    padding: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;
    font-weight: bold;
}
.pagination li:first-child a {
    background: rgb(204, 135, 45);
    color: #fff;
}
.pagination a.selected {
    background: rgb(204, 135, 45) !important;
    color: #fff !important;
}
.newsHr .likes {
    top: 100% !important;
    right: 0;
    font-family: Graduate;
    position: absolute;
    padding: 4px 6px !important;
    height: 18px !important;
    display: flex;
    align-items: center;
}

@media (min-width: 540px) { /* >= 540px */
    .newsHr .cart {
        height: 280px;
    }
}

@media (min-width: 768px) {
    .newsHr section {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .newsHr .cart {
        height: 230px;
        width: 360px;
        margin: 20px 0 0 0;
    }
}

@media (min-width: 820px) { /* >= 820px */
    .newsHr .cart {
        height: 240px;
        width: 385px;
        margin: 20px 0 0 0;
    }
}

@media (min-width: 912px) {
    .newsHr .cart {
        height: 280px;
        width: 426px;
    }
}

@media (min-width: 1024px) {
    .newsHr {
        padding: 0 0 0 0;
    }
    .newsHr .cart {
        width: 320px;
        height: 220px;
        margin: 20px 0 0 0;
    }
    .pagination {
        max-width: 50%;
    }
}

@media (min-width: 1680px) {
    .newsHr .cart {
        width: 385px;
        height: 250px;
    }
}